
import { defineComponent, PropType } from "vue";
import CustomMovement from "../models/CustomMovement";
import WayMixin from "../mixins/WayMixin";
import Stop from "../models/Stop";

export default defineComponent({
  name: "WayCustomMovement",
  mixins: [ WayMixin ],
  props: {
    customMovement: {
      type: Object as PropType<CustomMovement>,
      required: true,
    },
    stops: {
      type: Object as PropType<Stop[]>,
      required: true
    },
    startStationName: {
      type: String as PropType<string>,
      required: true
    },
    endStationName: {
      type: String as PropType<string>,
      required: true
    }
  },
  methods: {
    getStopProgress(stop: Stop) {
      return this.getProgress(stop, this.stops[this.stops.indexOf(stop) + 1], this.$ds.dateTimeInSeconds);
    }
  }
});
